.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogs img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
}

.dialogsItems {
    padding: 10px;
    color: white;
}

.dialogsItems .active {
    color: gold;
}

.messages {
    padding: 10px;
}

.messages .message {
    color: white;
}

.dialog {
    display: flex;
    align-items: center;
}

.dialog.senderMe {
    flex-direction: row-reverse;
}

.dialog img {
    margin-right: 8px;
}

.dialog.senderMe img {
    margin-right: 0;
    margin-left: 8px;
}

.sendMessage {
    display: flex;
    justify-content: flex-end;
    margin-top: 15px;
}
